import SortableList, { SortableItem } from 'react-easy-sort'
import arrayMove from 'array-move';
import FormLabel from '@mui/material/FormLabel';
import FormControl from '@mui/material/FormControl';
import FormGroup from '@mui/material/FormGroup';
import FormControlLabel from '@mui/material/FormControlLabel';
import FormHelperText from '@mui/material/FormHelperText';
import Switch from '@mui/material/Switch';
import { __experimentalNumberControl as NumberControl, SelectControl } from '@wordpress/components';
import { InspectorControls } from '@wordpress/block-editor';
import { Fragment } from "react";
import {
	PanelBody,
} from "@wordpress/components";
import cloneDeep from 'lodash.clonedeep';
import { InputBase } from '@mui/material';


export default function( {optionsJson, optionsSetting, setAttributes} ) {

	let currentVehicleOptions = JSON.parse(optionsJson);

	let listOfOptions = [];


	function fillListOfOption() {
		let result = [];
		for (let activeOption of optionsSetting.activeOptions) {
			for (let currentOption of currentVehicleOptions) {
				if (activeOption === currentOption.id) {
					result.push( {
						name: currentOption.name,
						icon: currentOption.icon,
					} );

					if(result.length === optionsSetting.limit) {
						return result;
					}
				}
			}
		}
		return result;
	}

	listOfOptions = fillListOfOption();

	let fullListOfOptions = {
		air_condition: {
			key: 'air_condition',
			title: 'Air Condition',
		},
		climate_control: {
			key: 'climate_control',
			title: 'Climate Control',
		},
		climate_control_two_zones: {
			key: 'climate_control_two_zones',
			title: 'Climate Control two zones',
		},
		luxury_climate_control: {
			key: 'luxury_climate_control',
			title: 'Luxury climate control',
		},
		sunroof: {
			key: 'sunroof',
			title: 'Sunroof',
		},
		panoramic_sunroof: {
			key: 'panoramic_sunroof',
			title: 'Panoramic sunroof',
		},
		moon_roof: {
			key: 'moon_roof',
			title: 'Moonroof',
		},
		push_button_start: {
			key: 'push_button_start',
			title: 'Push-button start',
		},
		proximity_key_entry_system: {
			key: 'proximity_key_entry_system',
			title: 'Proximity key entry system',
		},
		rear_parking_sensors: {
			key: 'rear_parking_sensors',
			title: 'Rear parking sensors',
		},
		parking_sensors: {
			key: 'parking_sensors',
			title: 'Parking sensors',
		},
		built_in_sat_nav: {
			key: 'built_in_sat_nav',
			title: 'Built-in sat nav',
		},
		mobile_phone_technology: {
			key: 'mobile_phone_technology',
			title: 'Mobile phone technology',
		},
		bluetooth: {
			key: 'bluetooth',
			title: 'Bluetooth',
		},
		usb: {
			key: 'usb',
			title: 'USB',
		},
		qi_wireless_charging: {
			key: 'qi_wireless_charging',
			title: 'Qi wireless charging',
		},
		audio_ipod: {
			key: 'audio_ipod',
			title: 'Audio/iPod',
		},
		cruise_control: {
			key: 'cruise_control',
			title: 'Cruise control',
		},
		adaptive_cruise_control: {
			key: 'adaptive_cruise_control',
			title: 'Adaptive cruise control',
		},
		apple_carplay: {
			key: 'apple_carplay',
			title: 'Apple CarPlay',
		},
		android_auto: {
			key: 'android_auto',
			title: 'Android Auto',
		},
		forward_collision_warning: {
			key: 'forward_collision_warning',
			title: 'Forward collision warning',
		},
		lane_departure_warning: {
			key: 'lane_departure_warning',
			title: 'Lane departure warning',
		},
		automatic_emergency_braking: {
			key: 'automatic_emergency_braking',
			title: 'Automatic emergency braking',
		},
		active_parking_assist: {
			key: 'active_parking_assist',
			title: 'Active parking assist',
		},
		automatic_high_beams: {
			key: 'automatic_high_beams',
			title: 'Automatic high beams',
		},
		adaptive_headlights: {
			key: 'adaptive_headlights',
			title: 'Adaptive Headlights',
		},
		camera_360_degree: {
			key: 'camera_360_degree',
			title: '360-degree camera',
		},
		rearview_camera: {
			key: 'rearview_camera',
			title: 'Rearview camera',
		},
		towing_hook: {
			key: 'towing_hook',
			title: 'Towing Hook',
		},
		towing_bar: {
			key: 'towing_bar',
			title: 'Towing Bar',
		}
	}

	const onSortEnd = (oldIndex, newIndex) => {
		optionsSetting.activeOptions = arrayMove(optionsSetting.activeOptions, oldIndex, newIndex);
		setAttributes({ optionsSetting: cloneDeep(optionsSetting) });
	}

	const handleChange = (event) => {
		let new_setting = [...optionsSetting.activeOptions];

		if(event.target.checked) {
			new_setting.push(event.target.name);
		} else {
			new_setting = optionsSetting.activeOptions.filter(e => e !== event.target.name);
		}

		optionsSetting.activeOptions = new_setting;

		setAttributes({ optionsSetting: cloneDeep(optionsSetting) });
	};

	const handleLimit = (value) => {
		optionsSetting.limit = parseInt( value );
		setAttributes({ optionsSetting: cloneDeep(optionsSetting) });
	}

	const handleSwitchIcon = (event) => {
		optionsSetting.displayIcon = event.target.checked;
		setAttributes({ optionsSetting: cloneDeep(optionsSetting) });
	}

	const handleSwitchHideTitle = (event) => {
		optionsSetting.hideTitle = event.target.checked;
		setAttributes({ optionsSetting: cloneDeep(optionsSetting) });
	}


	const handleDisplayType = (selectedValue) => {
		optionsSetting.displayType = selectedValue;
		setAttributes({ optionsSetting: cloneDeep(optionsSetting) });
	}

	const handleSetDelimiter = (event) => {
		optionsSetting.itemDelimiter = event.target.value;
		setAttributes({ optionsSetting: cloneDeep(optionsSetting) });
	}

	let resultList = []
	for(let option of optionsSetting.activeOptions) {
		if(fullListOfOptions.hasOwnProperty(option)) {
			fullListOfOptions[option].checked = true;
			resultList.push(fullListOfOptions[option]);
		}
	}

	for (const key in fullListOfOptions) {
		if(! optionsSetting.activeOptions.includes(key)) {
			fullListOfOptions[key].checked = false;
			resultList.push(fullListOfOptions[key]);
		}
	}

	const displayTypeList = [
		{
			label: 'List',
			value: 'list'
		},
		{
			label: 'Inline',
			value: 'inline'
		}
	];

	return (
		<div>
			<DisplayOptions itemDelimiter={optionsSetting.itemDelimiter} displayType={optionsSetting.displayType} displayIcon={optionsSetting.displayIcon} hideTitle={optionsSetting.hideTitle} items={listOfOptions} />
			<Fragment>
				<InspectorControls key="setting">
					<PanelBody>
						<NumberControl
							isDragEnabled={true}
							max={30}
							min={1}
							value={optionsSetting.limit}
							label={'Set limit of displayed options'}
							onChange={handleLimit}
						/>
						<hr/>
						<SelectControl
							class="display-type-selector"
							options={displayTypeList}
							label={'Select display type'}
							value={optionsSetting.displayType}
							onChange={handleDisplayType}
						/>
						{optionsSetting.displayType === 'inline' && (
							<InputBase
								class="rentsyst-option-inline-delimiter"
								label={'Delimiter between items'}
								value={optionsSetting.itemDelimiter}
								onChange={handleSetDelimiter}
							/>
						)}
					</PanelBody>
					<PanelBody>
						<FormControlLabel
							control={
								<Switch checked={optionsSetting.displayIcon} onChange={handleSwitchIcon} />
							}
							label={"Display icon"}
						/>
						<FormControlLabel
							control={
								<Switch checked={optionsSetting.hideTitle} onChange={handleSwitchHideTitle} />
							}
							label={"Hide title"}
						/>
					</PanelBody>
					<PanelBody>
						<FormControl component="fieldset" variant="standard">
							<FormLabel component="legend">Display settings</FormLabel>
							<FormHelperText>Enable/disable and sort options that you want to be display</FormHelperText>
							<FormGroup>
								<SortableList
									onSortEnd={onSortEnd}
									className="list"
									draggedItemClassName="dragged"
								>
									{resultList.map((item) => (
										<SortableItem key={item.key}>
											<div className="item">
												<FormControlLabel
													control={
														<Switch checked={item.checked} onChange={handleChange} name={item.key} />
													}
													label={item.title}
												/>
											</div>
										</SortableItem>
									))}
								</SortableList>
							</FormGroup>
						</FormControl>
					</PanelBody>
				</InspectorControls>
			</Fragment>
		</div>
	);

}

function DisplayOptions(props) {
	const items = props.items;
	const displayIcon = props.displayIcon;
	const itemDelimiter = props.itemDelimiter;
	const hideTitle = props.hideTitle;
	const displayType = props.displayType;

	if(displayType === 'list') {
		const listItems = items.map( ( item ) =>
			<li>
				{ displayIcon && (
					<i className={ 'fico rentsyst-option-icon fico-' + item.icon } title={ item.name }></i>
				) }
				{ ! hideTitle && (
					item.name
				) }
			</li>
		);
		return (
			<ul className="rentsyst-option-list">{ listItems }</ul>
		);
	} else {
		const listItems = items.map( ( item ) =>
			(
			<span>
				{ displayIcon && (
					<i className={ 'fico rentsyst-option-icon fico-' + item.icon } title={ item.name }></i>
				) }
				{ ! hideTitle && (
					item.name
				) }
				{itemDelimiter && items[items.length -1] !== item && (
					itemDelimiter
				)}
			</span>
			)
		);
		return <div className="rentsyst-options_wrap">{ listItems}</div>
	}
}
