/**
 * Retrieves the translation of text.
 *
 * @see https://developer.wordpress.org/block-editor/packages/packages-i18n/
 */
import { __ } from '@wordpress/i18n';

/**
 * React hook that is used to mark the block wrapper element.
 * It provides all the necessary props like the class name.
 *
 * @see https://developer.wordpress.org/block-editor/packages/packages-block-editor/#useBlockProps
 */
import { InspectorControls, useBlockProps } from '@wordpress/block-editor';

/**
 * Lets webpack process CSS, SASS or SCSS files referenced in JavaScript files.
 * Those files can contain any CSS code that gets applied to the editor.
 *
 * @see https://www.npmjs.com/package/@wordpress/scripts#using-css
 */
import './editor.scss';
import { Fragment } from "react";
import {
	Panel,
	PanelBody,
	SelectControl,
	ColorIndicator,
	Placeholder, Button,
} from '@wordpress/components';
import classnames from 'classnames';

import OptionsField from "./optionsField";

/**
 * The edit function describes the structure of your block in the context of the
 * editor. This represents what the editor will render when the block is used.
 *
 * @see https://developer.wordpress.org/block-editor/developers/block-api/block-edit-save/#edit
 *
 * @return {WPElement} Element to render.
 */

export default function Edit({
	attributes: { level, textAlign, isLink, rel, linkTarget, selectedField, optionsSetting },
	setAttributes,
	context: { postType, postId, queryId },
}) {

	let selectedFieldValue;

	let meta = [];
	if ( postType && postId ) {
		meta = wp.data.select( 'core' ).getEntityRecord( 'postType', 'vehicle', postId )['meta'];

		if (meta[selectedField]) {
			selectedFieldValue = meta[selectedField];
		}
	}

	let options = [
		{
			label: 'Brand',
			value: 'brand',
		},
		{
			label: 'Mark',
			value: 'mark',
		},
		{
			label: 'Group',
			value: 'group',
		},
		{
			label: 'Price',
			value: 'price',
		},
		{
			label: 'Transmission',
			value: 'transmission',
		},
		{
			label: 'Color',
			value: 'color',
			render: function( value ) {
				return (
					<ColorIndicator colorValue={'#' + value} />
				);
			}
		},
		{
			label: 'Body type',
			value: 'body_type',
		},
		{
			label: 'Year',
			value: 'year',
		},
		{
			label: 'Discount Price',
			value: 'discount_price',
		},
		{
			label: 'Number seats',
			value: 'number_seats',
		},
		{
			label: 'Number doors',
			value: 'number_doors',
		},
		{
			label: 'Large bags',
			value: 'large_bags',
		},
		{
			label: 'Small bags',
			value: 'small_bags',
		},
		{
			label: 'Min price',
			value: 'min_price',
		},
		{
			label: 'Prices by periods',
			value: 'periods_price',
		},
		{
			label: 'Insurance Deposit',
			value: 'insurance_deposit',
		},
		{
			label: 'Engine volume',
			value: 'volume_engine',
		},
		{
			label: 'Fuel',
			value: 'fuel',
		},
		{
			label: 'Work locations',
			value: 'locations',
		},
		{
			label: 'Thumbnail',
			value: 'thumbnail',
		},
		{
			label: 'Options',
			value: 'options_data',
			render: function( optionsJson ) {
				return (
					<OptionsField optionsJson={optionsJson} optionsSetting={optionsSetting} setAttributes={setAttributes} />
				);
			}
		},
	];


		for (let option of options) {
			if(option.value === selectedField) {
				if(!selectedFieldValue) {
					selectedFieldValue = option.label;
					break;
				} else if(option.hasOwnProperty('render')) {
					selectedFieldValue = option.render(selectedFieldValue);
				}
			}
		}


	const handleSelect = (selectedValue) => {
		setAttributes({selectedField: selectedValue});
	}

	const blockProps = useBlockProps( {
		className: classnames( {
			[ `has-text-align-${ textAlign }` ]: textAlign,
		} ),
	} );


	return (
		<>
		<Fragment>
			<InspectorControls key="setting">
				<Panel>
					<PanelBody>
					<SelectControl
						class="block-meta-field-selector"
						options={options}
						label={'Select custom field'}
						value={selectedField}
						onChange={handleSelect}
					/>
					</PanelBody>
				</Panel>
			</InspectorControls>
		</Fragment>
		<div { ...blockProps }>
			{selectedField && (
			<p>{selectedFieldValue}</p>
			)}
			{!selectedField && (
				<Placeholder
					label={__("Vehicle parameters block", "twentytwentytwo")}
				>
					<SelectControl
						class="block-meta-field-selector"
						options={options}
						label={'Select custom field'}
						onChange={handleSelect}
					/>
				</Placeholder>
			)}
		</div>
		</>
	);
}
